@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  background-color: #eee;
  margin: 0;
  font-family: 'Lato', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#resume-generator {
  max-width: -webkit-max-content;
  max-width: max-content;
  width: 100vw;
  margin: auto;
}

#resume-generator header {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  margin-top: 10px;
}

#resume-generator .submit-resume {
  display: block;
  padding: 8px;
  border: 1px solid #343332;
  border-radius: 8px;
  background-color: white;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
}

#contact-info {
  margin-left: 20px;
}

#contact-info h1,
#contact-info h2 {
  padding-bottom: 5px;
  margin-bottom: 15px;
  width: 95vw;
  max-width: 914px;
  border-bottom: 1px dotted #343332;
}

#contact-info h4 {
  margin: 0;
  margin-bottom: 10px;
}

#contact-form label {
  margin: 20px 0 20px 20px;
  display: inline-block;
}

#contact-form input {
  display: block;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #343332;
}

.contact-left {
  display: inline-block;
  margin-left: 20px;
}

.contact-right {
  display: inline-block;
  margin-left: 20px;
}

.contact-error {
  margin-left: 20px;
  color: #e83131;
}

#school-form p {
  white-space: pre-wrap;
}

#education {
  margin-left: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 100px;
}

#education h2 {
  padding-bottom: 5px;
  margin-bottom: 15px;
  width: 90vw;
  max-width: 737px;
  border-bottom: 1px dotted #343332;
}

#school-form label {
  display: inline-block;
  margin: 20px 0 20px 20px;
}

#school-form input,
#school-form textarea {
  display: block;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #343332;
}

#school-form #currentlyEnrolled {
  display: inline;
  margin-left: 15px;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

#school-form .school-description {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

#school-form .school-description textarea {
  font-size: 16px;
  width: 85vw;
  max-width: 699px;
  height: 100px;
}

#school-form button {
  display: block;
  padding: 8px;
  border: 1px solid #343332;
  border-radius: 8px;
  background-color: white;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 20px;
  margin: auto;
}

.saved-education {
  display: block;
  margin-left: 20px;
  margin-bottom: 40px;
}

.school-name {
  display: inline-block;
  margin: 0;
}

.dates-studying {
  margin: 0;
  margin-top: 5px;
  margin-left: 10px;
}

.saved-school-description {
  margin-top: 8px;
  white-space: pre-wrap;
}

.edit-school {
  margin-left: 20px;
  cursor: pointer;
}

.education-error {
  margin-left: 20px;
  color: #e83131;
}

@media (max-width: 600px) {
  #school-form #currentlyEnrolled {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  #school-form .school-description textarea {
    width: 75vw;
  }
}

#work-experience {
  margin-left: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

#work-experience h2 {
  padding-bottom: 5px;
  margin-bottom: 15px;
  width: 90vw;
  max-width: 737px;
  border-bottom: 1px dotted #343332;
}

#work-form label {
  display: inline-block;
  margin: 20px 0 20px 20px;
}

#work-form input,
#work-form textarea {
  display: block;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #343332;
}

#work-form #currentJob {
  display: inline;
  margin-left: 15px;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

#work-form .job-description {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

#work-form .job-description textarea {
  font-size: 16px;
  width: 85vw;
  max-width: 699px;
  height: 100px;
}

#work-form button {
  display: block;
  padding: 8px;
  border: 1px solid #343332;
  border-radius: 8px;
  background-color: white;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 20px;
  margin: auto;
}

.saved-jobs {
  display: block;
  margin-left: 20px;
  margin-bottom: 40px;
}

.company-name {
  display: inline-block;
  margin: 0;
}

.dates-worked {
  margin: 0;
  margin-top: 5px;
  margin-left: 10px;
}

.saved-work-description {
  margin-top: 8px;
  white-space: pre-wrap;
}

.edit-job {
  margin-left: 20px;
  cursor: pointer;
}

.work-error {
  margin-left: 20px;
  color: #e83131;
}

@media (max-width: 600px) {
  #work-form #currentJob {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  #work-form .job-description textarea {
    width: 75vw;
  }
}

